export default {
	data() {
		return {
			info: {}
		};
	},
	onLoad(option) {
		this.info = JSON.parse(option.info);
		console.log(this.info)
	},
	methods:{
		ViewImage(e) {
			uni.previewImage({
				urls: this.info.info.img,
				current: e.currentTarget.dataset.url
			});
		},
	}
}
